	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import yuangong from '@/views/yuangong/list'
	import kefangyuding from '@/views/kefangyuding/list'
	import tuifangjiesuan from '@/views/tuifangjiesuan/list'
	import kefangruzhu from '@/views/kefangruzhu/list'
	import kefangxinxi from '@/views/kefangxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import fangjianleixing from '@/views/fangjianleixing/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import yuangongRegister from '@/views/yuangong/register'
	import yuangongCenter from '@/views/yuangong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/yuangongCenter',
			name: '员工个人中心',
			component: yuangongCenter
		}
		,{
			path: '/news',
			name: '酒店资讯',
			component: news
		}
		,{
			path: '/yuangong',
			name: '员工',
			component: yuangong
		}
		,{
			path: '/kefangyuding',
			name: '客房预订',
			component: kefangyuding
		}
		,{
			path: '/tuifangjiesuan',
			name: '退房结算',
			component: tuifangjiesuan
		}
		,{
			path: '/kefangruzhu',
			name: '客房入住',
			component: kefangruzhu
		}
		,{
			path: '/kefangxinxi',
			name: '客房信息',
			component: kefangxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/fangjianleixing',
			name: '房间类型',
			component: fangjianleixing
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		]
	},
	{
		path: '/yuangongRegister',
		name: '员工注册',
		component: yuangongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
